<template>
  <div class="canvas_line" id="canvascontainer">
    <canvas id="cas"></canvas>
  </div>
</template>

<script>
export default {
  mounted () {
    /* eslint-disable */
    var canvas = document.getElementById("cas");
    var ctx = canvas.getContext("2d");
    resize();
    window.onresize = resize;

    // canvas
    function resize() {
      canvas.width = document.getElementById("canvascontainer").offsetWidth
      canvas.height = document.getElementById("canvascontainer").offsetHeight
    }

    var RAF = (function() {
      return (
        window.requestAnimationFrame ||
        window.webkitRequestAnimationFrame ||
        window.mozRequestAnimationFrame ||
        window.oRequestAnimationFrame ||
        window.msRequestAnimationFrame ||
        function(callback) {
          window.setTimeout(callback, 1000 / 60);
        }
      );
    })();

    // 
    var warea = { x: null, y: null, max: 20000 };
    window.onmousemove = function(e) {
      e = e || window.event;
      warea.x = e.clientX;
      warea.y = e.clientY;
    };
    window.onmouseout = function(e) {
      warea.x = null;
      warea.y = null;
    };
    // 
    // xyxa, yaxymax
    var dots = [];
    for (var i = 0; i < 200; i++) {
      var x = Math.random() * canvas.width;
      var y = Math.random() * canvas.height;
      var xa = Math.random() * 2 - 1;
      var ya = Math.random() * 2 - 1;
      dots.push({
        x: x,
        y: y,
        xa: xa,
        ya: ya,
        max: 6000
      });
    }
    // 100
    setTimeout(function() {
      animate();
    }, 100);

    // 
    function animate() {
      ctx.clearRect(0, 0, canvas.width, canvas.height);
      // 
      var ndots = [warea].concat(dots);
      dots.forEach(function(dot) {
        // 
        dot.x += dot.xa;
        dot.y += dot.ya;
        // 
        dot.xa *= dot.x > canvas.width || dot.x < 0 ? -1 : 1;
        dot.ya *= dot.y > canvas.height || dot.y < 0 ? -1 : 1;
        // 
        ctx.fillRect(dot.x - 0.5, dot.y - 0.5, 1, 1);
        // 
        for (var i = 0; i < ndots.length; i++) {
          var d2 = ndots[i];
          if (dot === d2 || d2.x === null || d2.y === null) continue;
          var xc = dot.x - d2.x;
          var yc = dot.y - d2.y;
          // 
          var dis = xc * xc + yc * yc;
          // 
          var ratio;
          // max
          if (dis < d2.max) {
            // 
            if (d2 === warea && dis > d2.max / 2) {
              dot.x -= xc * 0.03;
              dot.y -= yc * 0.03;
            }
            // 
            ratio = (d2.max - dis) / d2.max;
            // 
            ctx.beginPath();
            ctx.lineWidth = ratio / 2;
            ctx.strokeStyle = "rgba(255,255,255," + (ratio + 0.2) + ")";
            ctx.moveTo(dot.x, dot.y);
            ctx.lineTo(d2.x, d2.y);
            ctx.stroke();
          }
        }
        // 
        ndots.splice(ndots.indexOf(dot), 1);
      });
      RAF(animate);
    }
    /* eslint-enable */
  }
}
</script>

<style scoped>
.canvas_line {
  width: 100%;
  height: 100%;
  background: url(../../assets/images/big_banner.png);
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
}
</style>



